RONT CARD STYLES\r\n.card {\r\n    background-color: #bbb;\r\n    position: relative;\r\n    border-radius: 15px;\r\n    border: 2px solid $softDark;\r\n    margin: 0 auto;\r\n    justify-content: center;\r\n    text-align: center;\r\n    padding: auto;\r\n    \r\n}\r\n\r\n\r\n.card-img {\r\n    text-align: center;\r\n    display: block;\r\n}\r\n\r\n.type-icon {\r\n    border-radius: 100%;\r\n    margin: 0 auto;\r\n}\r\n\r\n.card-title {\r\n    color: $poke4;\r\n    font-family: $style3;\r\n    background-color: $dark;\r\n    width: 100%;\r\n}\r\n\r\n.card-body {\r\n    position: absolute;\r\n    bottom: 0;\r\n    left: 0;\r\n    margin-top: 0;\r\n    background-color: $dark;\r\n    box-shadow: inset 0px 0px 10px black;\r\n    font-weight: 100;\r\n    border-radius: 0px 0px 15px 15px;\r\n}\r\n\r\n.card-back-title {\r\n    position: absolute;\r\n    font-weight: 100;\r\n    top: 0;\r\n    left: 0;\r\n    width: 100%;\r\n    background: $softDark;\r\n    border-radius: 12px 12px 0px 0px;\r\n    color: $poke4;\r\n}\r\n\r\n.card-back-body {\r\n    font-family: $style3;\r\n    position: absolute;\r\n    bottom: 0;\r\n    left: 0;\r\n    border-radius: 0px 0px 15px 15px;\r\n    box-shadow: inset 0px 0px 2rem black;\r\n    width: 100%;\r\n    z-index: 0;\r\n}\r\n\r\n.card-id {\r\n    position: absolute;\r\n    top: 0;\r\n    left: 0;\r\n    border-radius: 12px 0px 0px 0px;\r\n    background: $dark;\r\n    clip-path: polygon(0 0, 100% 0%, 50% 50%, 0 50%);\r\n}\r\n\r\n.card-id-number{\r\n    position: absolute;\r\n    top: 0.5em;\r\n    left: 0.5em;\r\n    padding: 0 auto;\r\n    color: $primary;\r\n    \r\n}\r\n\r\n.card-back-body {\r\n    text-shadow: 0px 0px 5px black;\r\n}\r\n\r\n.flavor-text {\r\n    margin: 5%;\r\n    text-shadow: 2px 0px 3px $dark;\r\n}\r\n\r\n.stats-text {\r\n    position: absolute;\r\n    bottom: 0;\r\n    width: 100%;\r\n    height: auto;\r\n    border-radius: 0px 0px 15px 15px;\r\n    font-family: $style1;\r\n    font-weight: 100;\r\n    text-shadow: 5px 0px 5px $dark;\r\n    background:rgba(20, 20, 20, 0.364);\r\n\r\n    div ul {\r\n        width: 100%;\r\n    }\r\n}\r\n\r\n// ******* MOBILE RESPONSIVE ******* //\r\n@media only screen and (min-width: 360px) {\r\n}\r\n// ******* LAPTOP RESPONSIVE ******* //\r\n@media only screen and (min-width: 767px) {\r\n    .nav-button {\r\n        padding: 1rem;\r\n        font-size: 0.5em;\r\n    }\r\n\r\n    .card, .card-wrapper, .card-inner, .card, .card-back {\r\n        width: $cardwidth * 0.75;\r\n        height: $cardheight * 0.75;\r\n    }\r\n\r\n    .margin-cols {\r\n        margin-top: 2.5rem;\r\n        margin-bottom: 2.5rem;\r\n    }\r\n\r\n    .card-img {\r\n        padding-top: 1rem;\r\n    }\r\n    \r\n    .type-icon {\r\n        height: 3rem;\r\n        width: 3rem;\r\n    }\r\n\r\n    .type-innericon {\r\n        height: 1.5rem;\r\n        width: 1.5rem;\r\n    }\r\n    \r\n    .card-title {\r\n        font-size: 1rem;\r\n        padding-bottom: 0.5rem;\r\n    }\r\n    \r\n    .card-body {\r\n        margin: 2px;\r\n        width: 98%;\r\n        padding: 0.5rem;\r\n        font-size: 0.5rem;\r\n    }\r\n    .front-img {\r\n        margin-top: 2rem;\r\n        width: 7rem;\r\n        height: 7rem;\r\n    }\r\n    .card-id{\r\n        width: 5rem;\r\n        height: 4rem;\r\n    }\r\n    .card-id-number{\r\n        font-size: 0.8em;\r\n    }\r\n    .card-back-title {\r\n        height: 1.5rem;\r\n        font-size: 1em;\r\n        padding-top: 0.2rem;\r\n        letter-spacing: 3px;\r\n    }\r\n    \r\n    .card-back-body {\r\n        height: ($cardheight * 0.75) - 1.5rem;\r\n    }\r\n    .stats-text {\r\n        font-size: 0.5rem;\r\n        padding-bottom: 0.2rem;\r\n    }\r\n    .flavor-text {\r\n        font-size: 0.7rem;\r\n    }\r\n    h3 {\r\n        font-size: 1rem;\r\n    }\r\n}\r\n\r\n// ******* DESKTOP RESPONSIVE ******* //\r\n@media only screen and (min-width: 1370px) {\r\n    .nav-button {\r\n        padding: 1rem;\r\n        font-size: 0.8em